import React, { useState } from 'react'

function Pages({ pages, setPage, pageD }) {
    return (
        <ul className="pages">
            {
                pages.map((page, i) => {
                    return (
                        <li key={i}>
                            <button
                                className={pageD === page ? "active" : ""}
                                onClick={() => setPage(page)}>{page}</button>
                        </li>
                    )
                })
            }
        </ul>
    )
}

export default Pages;